.download {
  min-width: 1260px;
  max-width: 1280px;
  margin: 0 auto;

  background-color: $download-bg;

  &__header {
    font-size: 20px;
    font-family: "GothamRounded-Bold", "Arial Black", sans-serif;
    color: $font-black;
    letter-spacing: 3px;
  }

  &__text {
    margin-top: 7px;

    font-size: 12px;
    line-height: 21px;
    font-family: "GothamRounded-Medium", "Arial", sans-serif;
    color: $font-black;
    letter-spacing: 1.8px;
  }

  &__download-buttons {
    margin-top: 43px;
  }
}

.download-container {
  width: 896px;
  margin: 0 auto;
  padding-top: 87px;
  padding-bottom: 116px;
}

.download-text {
  text-align: center;
}
