.banner {
  min-width: 1260px;
  max-width: 1280px;

  &__header {
    font-size: 36px;
    font-family: "GothamRounded-Bold", "Arial Black", sans-serif;
    color: $font-white;
    letter-spacing: 5.4px;
  }

  &__subheader {
    margin-top: 7px;

    font-size: 16px;
    font-family: "GothamRounded-Book", "Arial", sans-serif;
    color: $font-white;
    letter-spacing: 2.5px;
  }

  &__start-btn {
    margin-top: 25px;
  }
}

.banner-container {
  padding-top: 238px;
  padding-bottom: 200px;

  text-align: center;
}
