.header {
  min-width: 1260px;
  max-width: 1280px;

  &__link {
    margin-top: 30px;

    font-size: 16px;
    font-family: "GothamRounded-Bold", "Arial Black", sans-serif;
    color: $font-white;
    letter-spacing: 2.7px;
  }

  &__photoapp {
    margin-right: 98px;
    margin-left: 62px;
  }

  &__design {
    margin-right: 73px;
    margin-left: 97px;
  }
}

.header-container {
  display: flex;
  justify-content: center;
  width: 820px;
  margin: 0 auto;
  padding-top: 40px;
  padding-left: 25px;
}

.logo {
  display: inline-block;
  width: 53px;
  height: 55px;

  background-image: url("../img/logo.png");
  background-repeat: no-repeat;
  background-size: cover;
}
