.btn {
  display: inline-block;

  font-size: 12px;
  font-family: "GothamRounded-Medium", "Arial", sans-serif;
  color: $font-white;
  letter-spacing: 1.8px;

  border-radius: 3px;
}

.btn--green {
  background-color: $btn-green;

  transition: background-color 0.2s;

  &:hover {
    background-color: $btn-green-hover;

    transition: background-color 0.2s;
  }
}

.start-btn {
  padding: 17px 30px;
}

.download-buttons {
  display: flex;
  justify-content: space-between;
  width: 619px;
  margin: 0 auto;
}

.btn-appstore {
  position: relative;

  padding-top: 22px;
  padding-right: 131px;
  padding-bottom: 16px;
  padding-left: 23px;

  font-size: 15px;
  color: $font-white;
  letter-spacing: 3px;

  background-color: $download-button;

  transition: background-color 0.2s;

  &:hover {
    background-color: $download-button-hover;

    transition: background-color 0.2s;
  }

  &__icon {
    position: absolute;
    top: 0;
    right: 0;

    display: inline-block;
    padding: 15px 16px 14.5px 16px;

    background-color: $download-icon;
    border-radius: 0 3px 3px 0;
  }
}

.btn-playstore {
  position: relative;

  padding-top: 19px;
  padding-right: 125px;
  padding-bottom: 10px;
  padding-left: 29px;

  font-size: 18px;
  color: $font-white;
  letter-spacing: 2.8px;

  background-color: $download-button;

  transition: background-color 0.2s;

  &:hover {
    background-color: $download-button-hover;

    transition: background-color 0.2s;
  }

  &__icon {
    position: absolute;
    top: 0;
    right: 0;

    display: inline-block;
    padding: 12px 14px 12.5px 17px;

    background-color: $download-icon;
    border-radius: 0 3px 3px 0;
  }
}
