.footer {
  min-width: 1260px;
  max-width: 1280px;
  margin: 0 auto;

  background-color: $footer-bg;

  &__link {
    font-size: 14px;
    font-family: "GothamRounded-Bold", "Arial Black", sans-serif;
    color: $font-black;
    letter-spacing: 2.3px;
  }

  &__copyright {
    margin-top: 20px;
  }
}

.footer-links {
  display: flex;
}

.separator {
  &::before {
    content: "|";

    display: inline-block;
    margin: 0 4px;

    font-size: 14px;
    font-family: "GothamRounded-Bold", "Arial Black", sans-serif;
    color: $font-green;
    letter-spacing: 6px;

    cursor: default;
  }
}

.footer-container {
  display: flex;
  justify-content: space-between;
  width: 1030px;
  margin: 0 auto;
  padding-top: 43px;
  padding-bottom: 33px;
}

.footer-info {
  display: inline-block;
}

.copyright {
  font-size: 14px;
  font-family: "GothamRounded-Bold", "Arial Black", sans-serif;
  color: $font-black;
  letter-spacing: 2.1px;
}

.footer-logo {
  display: inline-block;

  &__img {
    width: 93%;
    height: auto;
  }
}
