.design {
  min-width: 1260px;
  max-width: 1280px;
  margin: 0 auto;

  background-color: $design-bg;

  &__header {
    font-size: 18px;
    font-family: "GothamRounded-Bold", "Arial Black", sans-serif;
    color: $font-green;
    letter-spacing: 2.2px;
  }

  &__text {
    margin-top: 36px;

    font-size: 12px;
    line-height: 22px;
    font-family: "GothamRounded-Book", "Arial", sans-serif;
    color: $font-black;
    letter-spacing: 2px;
    word-spacing: -1px;
  }

  &__btn {
    margin-top: 37px;
    padding: 18px 45px;
  }
}

.design-container {
  display: flex;
  justify-content: space-between;
  width: 1128px;
  height: 558px;
  margin: 0 auto;
  padding-top: 141px;
  overflow: hidden;
}

.design-text {
  display: inline-block;
  width: 437px;

  text-align: left;
}

.design-img {
  position: relative;

  display: inline-block;
  width: 314px;
  height: 617px;
  margin-top: 5px;
  margin-left: 47px;

  background-image: url("../img/design-img.png");
  background-repeat: no-repeat;
  background-size: cover;
}

.design-slider {
  margin-top: 43px;

  &__count {
    display: block;

    font-size: 12px;
    font-family: "GothamRounded-Bold", "Arial", sans-serif;
    color: $font-black;
    letter-spacing: 2px;
  }

  &__arrow-l {
    margin-right: 23px;
  }

  &__arrow-r {
    margin-left: -4px;
  }
}
