.prelude {
  min-width: 1260px;
  max-width: 1280px;
  margin: 0 auto;

  background-image: url("../img/prelude-bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;

  &__header {
    display: block;
    width: 388px;

    font-size: 18px;
    font-family: "GothamRounded-Bold", "Arial Black", sans-serif;
    color: $font-green;
    letter-spacing: 2.6px;
  }

  &__text {
    display: block;
    width: 430px;
    padding-top: 40px;

    font-size: 13px;
    line-height: 24px;
    font-family: "GothamRounded-Book", "Arial", sans-serif;
    text-align: left;
    color: #373737;
    letter-spacing: 1px;
    word-spacing: 2px;
  }

  &__img {
    position: absolute;
    top: 91px;
    right: 0;

    display: inline-block;
    width: 642px;
    height: 701px;

    background-image: url("../img/prelude-img.png");
    background-repeat: no-repeat;
    background-size: cover;
  }

  &__start-btn {
    margin-top: 40px;
    padding: 18px 43px;
  }
}

.prelude-container {
  position: relative;

  width: 1024px;
  height: 492px;
  margin: 0 auto;
  padding-top: 109px;
  padding-bottom: 90px;
  overflow: hidden;
}

.prelude-text {
  display: inline-block;
}
