.community {
  min-width: 1260px;
  max-width: 1280px;
  margin: 0 auto;

  background-color: $community-bg;

  &__images {
    margin-left: 80px;
  }
}

.community-container {
  width: 1024px;
  margin: 0 auto;
  padding-top: 89px;
  padding-bottom: 119px;
}

.community-slider {
  text-align: right;

  &__arrow-l {
    margin-right: 18px;
  }

  &__arrow-r {
    margin-right: 2px;
    margin-left: -4px;
  }
}

.community-inner {
  display: flex;
  align-items: center;
  margin-top: 5px;
}

.community-images {
  display: flex;
  justify-content: space-between;
  width: 765px;
}

.community-intro {
  display: inline-block;
  width: 177px;

  text-align: center;
  letter-spacing: 1.5px;

  &__header {
    margin-top: 24px;

    font-size: 16px;
    font-family: "GothamRounded-Bold", "Arial Black", sans-serif;
    color: $font-black;
    letter-spacing: 2px;
  }

  &__text {
    margin-top: 19px;

    font-size: 10px;
    font-family: "GothamRounded-Medium", "Arial", sans-serif;
    color: $font-black;
  }
}
