.subscribe {
  min-width: 1260px;
  max-width: 1280px;
  margin: 0 auto;

  text-align: center;

  background: url("../img/subscribe-bg.jpg") no-repeat top;
  background-size: cover;

  &__header {
    font-size: 24px;
    font-family: "GothamRounded-Bold", "Arial Black", sans-serif;
    color: $font-white;
    letter-spacing: 3.6px;
  }

  &__form {
    margin-top: 28px;
  }

  &__email {
    width: 343px;
    padding-top: 21px;
    padding-right: 90px;
    padding-bottom: 20px;
    padding-left: 19px;

    font-size: 16px;
    font-family: "GothamRounded-Medium", "Arial", sans-serif;
    color: $font-black;
    letter-spacing: 2.4px;

    border-radius: 3px;
  }

  &__email::placeholder {
    font-size: 16px;
    font-family: "GothamRounded-Medium", "Arial", sans-serif;
    color: $font-black;
  }
}

.subscribe-container {
  padding-top: 347px;
  padding-bottom: 325px;
}

.subscribe-form {
  position: relative;

  display: inline-block;
}

.subscribe-check {
  position: absolute;
  top: 0;
  right: 0;

  padding-top: 22px;
  padding-right: 22px;
  padding-bottom: 16.4px;
  padding-left: 22px;

  background-color: $check-bg;
  border-radius: 0 3px 3px 0;
}
